@import url(https://fonts.googleapis.com/css?family=Roboto:300);
* {
  margin: 0;
  padding: 0;
}

.App {
  min-height: 90vh;
}

.login-page{width:360px;padding:8% 0 0;margin:auto}.form{position:relative;z-index:1;background:#fff;max-width:360px;margin:0 auto 100px;padding:45px;text-align:center;box-shadow:0 0 20px 0 rgba(0,0,0,.2),0 5px 5px 0 rgba(0,0,0,.24)}.form input{font-family:"Roboto",sans-serif;outline:0;background:#f2f2f2;width:100%;border:0;margin:0 0 15px;padding:15px;box-sizing:border-box;font-size:14px}.form button{font-family:"Roboto",sans-serif;text-transform:uppercase;outline:0;background:#4caf50;width:100%;border:0;padding:15px;color:#fff;font-size:14px;transition:all .3 ease;cursor:pointer}.form button:hover,.form button:active,.form button:focus{background:#43a047}.form .message{margin:15px 0 0;color:#b3b3b3;font-size:12px}.form .message a{color:#4caf50;text-decoration:none}.error{margin:0rem 0rem .1rem .5rem;text-align:left;color:red;font-size:.6rem}.error-top{margin:0rem 0rem .5rem .5rem;text-align:left;color:red}
.top {
  width: 100%;
  height: 50px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  background-color: #333;
  color: #fff;
  z-index: 2;
}

.topLeft,
.topRight {
  flex: 3 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.topCenter {
  flex: 6 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.topList {
  display: flex;
  list-style: none;
  justify-content: center;
  align-items: center;
}

.topListItem {
  padding: 10px;
  cursor: pointer;
  color: #fff;
  transition: 0.5s;
}

.topListItem:hover {
  background-color: #eee;
  color: #333;
  border-radius: 5px;
}

a {
  text-decoration: none;
  color: inherit;
}

.ft-legal{padding:.9375rem 1.875rem;background-color:#333;color:#eee}.ft-legal-list{width:100%;display:flex;flex-wrap:wrap}.ft-legal-list li{margin:.125rem .625rem;white-space:nowrap}.ft-legal-list li:nth-last-child(2){flex:1 1}footer li{list-style:none}footer a{text-decoration:none;color:#eee}
.profile-container{display:flex;align-items:center;justify-content:space-around}.edit-profile-container{max-width:1080px;width:95%;margin:auto}.form{margin-right:0;margin-left:0}.sidenav{color:#333;border-bottom-right-radius:25px;height:86vh;left:0;overflow-x:hidden;padding-top:20px;top:70px;width:250px}.profile{margin-bottom:20px;margin-top:-12px;text-align:center}.profile img{border-radius:50%;box-shadow:0px 0px 5px 1px gray}.name{font-size:20px;font-weight:bold;padding-top:20px}.job{font-size:16px;font-weight:bold;padding-top:10px}.url,hr{text-align:center}.url hr{margin-left:20%;width:60%}.url a{color:#818181;display:block;font-size:20px;margin:10px 0;padding:6px 8px;text-decoration:none}.url a:hover,.url .active{background-color:#e8f5ff;border-radius:28px;color:#000;margin-left:14%;width:65%}.main{margin-top:2%;font-size:28px;padding:0 10px;width:58%}.main h2{color:#333;font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif;font-size:24px;margin-bottom:10px}.main .card{background-color:#fff;border-radius:18px;box-shadow:1px 1px 8px 0 gray;height:auto;margin-bottom:20px;padding:20px 0 20px 50px}.main .card table{border:none;font-size:16px;height:270px;width:80%}.edit{position:absolute;color:#e7e7e8;right:14%}
