body{background:#eee}.date{font-size:11px}.comments{width:100%;min-width:300px;background:#fff;padding:20px 30px;border-radius:10px}.comments h2{font-size:2.3em;margin-bottom:0}.fs-12{font-size:12px}.liked{color:blue}.shadow-none{box-shadow:none}.name{color:#007bff}.cursor:hover{color:blue}.cursor{cursor:pointer}.textarea{resize:none}.post-text{font-size:1rem}
.comment{position:relative;padding:10px 20px;background:#efefef;border-radius:10px;margin-bottom:15px}.comment .timestamp{color:#777;position:absolute;right:20px;top:10px}.comment h4,.comment p{margin:10px}
.post-form{margin:1rem;padding:.5rem 2rem;box-shadow:0px 0px 5px 1px gray}
.users{padding:10px 10px 0 10px;margin:0;list-style:none;display:flex;flex-flow:row wrap;justify-content:space-around}
*{margin:0;padding:0;box-sizing:border-box}.user{flex:0 0 auto;margin-bottom:10px;padding:10px;color:#fff;text-align:center}.card-user{position:relative;width:300px;height:200px;background:#fff;border-radius:10px;background:rgba(255,255,255,.1);border-top:1px solid rgba(255,255,255,.5);-webkit-backdrop-filter:blur(15px);backdrop-filter:blur(15px);box-shadow:0 15px 25px rgba(0,0,0,.1)}.img-bx{position:absolute;top:0;left:0;width:100%;height:100%;border-radius:10px;overflow:hidden;transform:translateY(30px) scale(0.5);transform-origin:top}.img-bx img{position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover}.content{position:absolute;width:100%;height:100%;display:flex;justify-content:center;align-items:flex-end;padding-bottom:30px}.content .detail{display:flex;justify-content:center;align-items:center;flex-direction:column;text-align:center}.content .detail h2{color:#444;font-size:1.6em;font-weight:bolder}.content .detail h2 span{font-size:.7em;color:#03a9f4;font-weight:bold}.sci{position:relative;display:flex;margin-top:5px}.sci li{list-style:none;margin:4px}.sci li a{width:45px;height:45px;display:flex;justify-content:center;align-items:center;border-radius:50%;background:transparent;font-size:1.5em;color:#444;text-decoration:none;box-shadow:0 3px 6px rgba(0,0,0,.16),0 3px 6px rgba(0,0,0,.23);transition:.5s}.sci li a:hover{background:#03a9f4;color:#fff}
